.specialcard {
  border-radius: 16px;
  width: 540px;
  height: 540px;

  position: relative;
  background-size: contain;
  overflow: hidden;

  box-shadow: 0 5px 50px rgba(0, 0, 0, 0.85);
}
.specialcard:before {
  content: "";
  position: absolute;

  width: 100%;
  height: 100%;
  top: 0;
  background-color: rgba(0, 0, 0, 0.7);
}
.specialcard .img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
}
.specialcard .img span {
  width: 25%;
  height: 100%;
  /* background: url(https://firebasestorage.googleapis.com/v0/b/update-image.appspot.com/o/images%2FIMG_20200330_110953_746.jpg?alt=media&token=53490ca2-3aad-4a1e-93c6-c7d7ad8b65a6); */
  transition: 0.5s;
  background-size: cover;
}
.specialcard .img span:nth-child(1) {
  background-position: 0;
  transition-delay: 0;
}
.specialcard .img span:nth-child(2) {
  background-position: 20%;
  transition-delay: 0.1s;
}
.specialcard .img span:nth-child(3) {
  background-position: 40%;
  transition-delay: 0.2s;
}
.specialcard .img span:nth-child(4) {
  background-position: 60%;
  transition-delay: 0.3s;
}
.specialcard .img span:nth-child(5) {
  background-position: 80%;
  transition-delay: 0.2s;
}
.specialcard .img span:nth-child(6) {
  background-position: 100%;
  transition-delay: 0.3s;
}

.specialcard:hover .img > span {
  transform: translateY(-100%);
}
.specialcard:hover .specialcontent {
  transform: translateY(0%);
  transition: 1s;
  transition-delay: 0.1s;
}

.specialcontent {
  box-sizing: border-box;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  color: white;
  padding: 20px 20px;
  width: 100%;
  height: 100%;
  transform: translateY(100%);
}
