.journey {
  font-family: "Raleway", sans-serif;
  /* background-image: radial-gradient(circle at center, #899dc4, #495d84); */
  background-size: cover;
  background-repeat: no-repeat;
  height: 220px;
  width: 100%;
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
  /* background: radial-gradient(ellipse at bottom, #1b2735, #090a0f); */
  font-weight: 300;
}

.journeyall {
  display: flex;
  perspective: 10px;
  transform: perspective(300px) rotateX(20deg);
  will-change: perspective;
  perspective-origin: center center;
  transition: all 1.3s ease-out;
  justify-content: center;
  transform-style: preserve-3d;
}

.journeyall:hover {
  perspective: 1000px;
  transition: all 1.3s ease-in;
  transform: perspective(10000px) rotateX(0deg);
}
.journeyall:hover .journeytext {
  opacity: 1;
}
.journeyall:hover > div {
  opacity: 1;
  transition-delay: 0s;
}
.journeyall:hover .journeyexplainer {
  opacity: 0;
}

.journeyleft,
.journeycenter,
.journeyright,
.journeylefter,
.journeyrighter {
  width: 200px;
  height: 150px;
  transform-style: preserve-3d;
  border-radius: 10px;
  border: 1px solid #fff;
  box-shadow: 0 0 20px 5px rgba(100, 100, 255, 0.4);
  opacity: 0;
  transition: all 0.3s ease;
  transition-delay: 1s;
  position: relative;
  background-position: center center;
  background-size: contain;
  background-repeat: no-repeat;
  background-color: #58d;
  cursor: pointer;
  background-blend-mode: color-burn;
}
@media only screen and (max-width: 414px) {
  .journeyall {
    transform: rotateZ(90deg) scale(0.4) !important ;
  }
  .journey {
    height: 459px !important;
  }
}
