.hexagonalAnimation {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  overflow: hidden;
  background-color: transparent;
}

.hexagonalAnimationul {
  width: 70vmin;
  height: 70vmin;
  position: relative;
  list-style: none;
}

.hexagonalAnimationli {
  --h: calc(100% / 3);
  --w: calc(1.15470053838 * var(--h));
  --a: calc(0.28867513459 * var(--h));
  --d: calc(0.57735026919 * var(--h));
  height: var(--h);
  width: var(--w);
  position: absolute;
}

.hexagonalAnimationimg {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;

  --aw: 24.99999999999%;
  -webkit-clip-path: polygon(
    var(--aw) 0,
    0 50%,
    var(--aw) 100%,
    calc(100% - var(--aw)) 100%,
    100% 50%,
    calc(100% - var(--aw)) 0
  );
  clip-path: polygon(
    var(--aw) 0,
    0 50%,
    var(--aw) 100%,
    calc(100% - var(--aw)) 100%,
    100% 50%,
    calc(100% - var(--aw)) 0
  );
}

.hexagonalAnimationli:nth-of-type(1) {
  left: calc(50% - var(--a) - var(--d) * 0.5);
  top: 0;
}
.hexagonalAnimationli:nth-of-type(2) {
  left: calc(50% - var(--a) * 2 - var(--d) * 1.5);
  top: calc(var(--h) * 0.5);
}
