  left: calc(50% - var(--a) - var(--d) * 0.5);
  top: calc(var(--h) * 2);
}
.hexagonalAnimationli:nth-of-type(5) {
  left: calc(50% + var(--d) * 0.5);
  top: calc(var(--h) * 0.5);
}
.hexagonalAnimationli:nth-of-type(6) {
  left: calc(50% + var(--d) * 0.5);
  top: calc(var(--h) * 1.5);
}
